import React from 'react';
import styles from './index.css';
import { devPath, searchPageUrl } from '@src/constants';
// import { Grounds } from '@ifeng/client_grounds';
const backImg = 'https://x0.ifengimg.com/ucms/2022_12/BAF727636C099486435E9B4A90987917304C98CC_size3_w180_h180.png';
const searchImg = 'https://x0.ifengimg.com/ucms/2022_12/73D938D97E50E82E073ED160D3DB86789C6E84CD_size11_w180_h180.png';

const Header = () => {
    const goOtherPage = () => {
        if (window.grounds) {
            Grounds.dispatch({
                link: {
                    type: 'web',
                    url: `${devPath}/search/?isFull=0&ifengsp=1&needpage=1`,
                },
                pageLogParams: {
                    ref: 'phmovie',
                },
            });
        } else {
            // const finalUrl = `${'http://172.30.20.17:3002/mobile/phtv/loc/tvTheater'}/search`;
            // window.location.href = finalUrl;
        }
    };
    const goBack = () => {
        if (window.grounds) {
            Grounds.close();
        }
    };

    return (
        <div className={styles.header}>
            <img
                onClick={() => {
                    goBack();
                }}
                className={styles.headerBackImg}
                src={backImg}
            />
            <div className={styles.headerText}></div>
            <img
                onClick={() => {
                    goOtherPage();
                }}
                className={styles.headerSearch}
                src={searchImg}
            />
        </div>
    );
};

export default Header;
