import React, { createContext } from 'react';
export const ListBoxData = createContext({});
export const CHANGE_LIST = 'CHANGE_LIST';
export const ADD_LIST = 'ADD_LIST';
// 

export const SHOW_TIP = 'SHOW_TIP';
export const reducer = (state, action) => {
  switch (action.type) {
    case CHANGE_LIST:
      // eslint-disable-next-line no-case-declarations
      let searchBoxList = action.data.searchBoxList;
      // eslint-disable-next-line no-case-declarations
      const obj = {};

       searchBoxList = [...searchBoxList].reduce((item, next) => {
        // eslint-disable-next-line no-unused-expressions
        obj[next.id] ? '' : (obj[next.id] = true && item.push(next));

        return item;
      }, []);

return { ...state, ...action.data, searchBoxList, clickSearch: action.data.clickSearch, };
    case SHOW_TIP:
      return { ...state, showTip: action.data.showTip };
    default:
      return state;
  }
};
