import React, { useContext } from 'react';
import styles from './index.css';
import PropTypes from 'prop-types';
import exposureHoc from '@src/components/ExposureHoc';
import { devPath, detailPageUrl, Tip } from '@src/constants';
import { newGoPage } from '@src/utils';
import { groundsDispatch } from '@src/utils/app';
// import { Grounds } from '@ifeng/client_grounds';

// 
const noSearchDataImg =
    'https://x0.ifengimg.com/ucms/2022_12/42C1E1133692B9B1E12FF93C55117471EDDA70EC_size147_w924_h924.png';
// 
const noMeetDataImg =
    'https://x0.ifengimg.com/ucms/2022_12/B9DF383D5E5A771BBE640EFD0D80947EABC88C20_size159_w924_h924.png';

import { noInternetImg } from '@src/components/searchListBox/noInternetImg';
const { formatImage, formatUrl } = require('@ifeng/public_method');
const ListBox = ({ listData }) => {
    const { data } = useContext(listData);

    return (
        <>
            <div className={styles.list}>
                {(data.showTip === Tip.SUCCESS || data.showTip === Tip.NOMORE || data.showTip === Tip.LOADING) &&
                    data?.searchBoxList.map((item, index) => {
                        return (
                            <div key={index}>
                                {data.FormatEventData ? (
                                    <div key={item.id}>
                                        <NewBoxItem
                                            searchref={data.searchValue}
                                            eventData={data.FormatEventData({
                                                id: item.id, // id:post_xxxxxpost_123
                                                rnum: index, // 0 
                                                ch: data.searchValue || '', // ID,
                                            })}
                                            start={data?.searchBoxList.length > 0}
                                            index={index}
                                            item={item}
                                        />
                                    </div>
                                ) : (
                                    <div key={item.id}>
                                        <BoxItem searchref={data.searchValue} index={index} item={item} />
                                    </div>
                                )}
                            </div>
                        );
                    })}
                {data.showTip === Tip.NODATA && <img src={noMeetDataImg} className={styles.tip} />}
                {data.showTip === Tip.NOSEARCHDATA && <img src={noSearchDataImg} className={styles.tip} />}
                {data.showTip === Tip.NOINTERNET && (
                    <div
                        style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}
                        className={styles.tip}>
                        <img className={styles.noInternetImg} src={noInternetImg} />
                        <div className={styles.noInternetText1}></div>
                        <div
                            onClick={() => {
                                data.clickSearch();
                            }}
                            className={styles.noInternetText2}>
                             
                        </div>
                    </div>
                )}
                {data.showTip === Tip.LOADING && (
                    <div className={styles.loadingTip}>
                        <img className={styles.loadingTipImg} src={require('./loading.png')} />
                        <div className={styles.loadingTipText}></div>
                    </div>
                )}
            </div>
            {/* {data.showTip === Tip.LOADING && <div className={styles.loading} ></div>} */}
        </>
    );
};
const BoxItem = ({ item, index, searchref }) => {
    const goOtherPage = () => {
        const url = `${devPath}/details/${item.id.replace('ucms_', '')}?id=phmovie_${item.id.replace(
            'ucms_',
            '',
        )}&rnum=${index}&ref=${encodeURIComponent(
            `phmoviesrh_${searchref}`,
        )}&showtype=phposter&type=phmovie&needpage=1`;

        Grounds.dispatch({
            link: {
                type: 'web',
                url,
            },
            pageLogParams: {
                ref: `phmoviesrh_${searchref}`,
            },
        });
    };

    return (
        <div
            onClick={() => {
                goOtherPage();
            }}
            className={styles.box}>
            <div className={styles.boxItem}>
                <img src={item.thumbnail} className={styles.boxItemImg} />
                <div className={styles.gary} />
                <div className={styles.left}>{item.tags}</div>
                <div className={styles.right}>{item.numText}</div>
            </div>
            <div className={styles.title}>{item.title}</div>
            <div className={styles.dec}>{item.description}</div>
        </div>
    );
};

const NewBoxItem = exposureHoc(BoxItem);

BoxItem.propTypes = {
    item: PropTypes.object,
    index: PropTypes.number,
    searchref: PropTypes.string,
};
ListBox.propTypes = {
    listData: PropTypes.object,
};
export default ListBox;
