import React, { useEffect } from 'react';
import exposure from '@src/hooks/intersectionObserver';

// hoc
const exposureHoc = WrappedComponent => props => {
    // eslint-disable-next-line react/prop-types
    const { index, start = true, sendData = true, eventData } = props;
    // did mount 

    useEffect(
        () => {
            // eslint-disable-next-line no-unused-expressions
            start && sendData && exposure.add({ el: document.getElementById(`paper-content-${index}`) });
        },
        [start],
    );

    const value = { eventData };
    // dom data 

    return (
        <div id={`paper-content-${index}`} data-param={JSON.stringify(value)}>
            <WrappedComponent {...props} />
        </div>
    );
};

export default exposureHoc;
