/* eslint-disable no-unused-expressions */
// polyfill 
import 'intersection-observer';
import { senPageInfoStatistics } from '@/src/utils/app';

// 
IntersectionObserver.prototype['THROTTLE_TIMEOUT'] = 300;

class Exposure {
    constructor(maxNum = 10) {
        this.dataList = [];
        this.maxNum = maxNum;
        this.time = 200; // 
        this.maxNum = maxNum; // 
        this._observer = null;
        this._timer = null;
        this.init();
    }

    // 
    init() {
        const self = this;

        this._observer = new IntersectionObserver(
            entries => {
                entries.forEach(entry => {
                    if (entry.isIntersecting) {
                        // 
                        try {
                            self._timer && clearTimeout(self._timer); // 
                            const eventData = entry.target.attributes['data-param'].value;

                            JSON.parse(eventData)?.eventData && self.dataList.push(JSON.parse(eventData)?.eventData);
                            // DOM
                            self._observer.unobserve(entry.target);
                            // 
                            if (self.dataList.length >= self.maxNum) {
                                self.send();
                            } else if (self.dataList.length > 0) {
                                self._timer = setTimeout(() => {
                                    // 
                                    self.send();
                                }, self.time);
                            }
                        } catch (err) {
                            console.log(err);
                        }
                    }
                });
            },
            {
                root: document.querySelector('#app'),
                rootMargin: '0px',
                threshold: 1, // dom 0 - 1
            },
        );
    }

    // 
    add(entry) {
        const { el } = entry || {};

        this._observer && this._observer.observe(el);
    }

    // 
    send() {
        const data = this.dataList.slice(0, this.maxNum);
        const pinfoArray = data.map(item => {
            return ({
                ...item,
            });
        });

        senPageInfoStatistics(pinfoArray);

        this.dataList = [];
    }

    // 
    beforeUnmount() {
        console.log('------');
        const data = this.dataList;
    }
}

export default new Exposure();
