import React, { useContext, useEffect, useRef, useState } from 'react';
import styles from './index.css';
import { setH5PageTouchArea, setNoSlideArea, setPageNoActiveListener } from '@src/utils/app';
import { CHANGE_LIST, ListBoxData, SHOW_TIP, } from '@src/mobile/allContent/layout/store';
// import { getSearchResultApi } from '@src/mobile/allContent/layout/service';
import { Tip } from '@src/constants';
const tabBox = () => {
  const { dispatch, data } = useContext(ListBoxData);
  const playerRef = useRef();
  const getList = async() => {

    // const res = await getSearchResultApi({ tags: data.twoTab[data.activeTwoTab].type, [data.oneTab[data.activeOneTab].value]: data.oneTab[data.activeOneTab].type });

    // if (res.item?.length > 0) {
    //   dispatch({
    //     type: CHANGE_LIST,
    //     data: { searchBoxList: res.item, showTip: Tip.SUCCESS, totalPage: res.totalPage },
    //   });
    // } else {
    //   dispatch({
    //     type: CHANGE_LIST,
    //     data: { searchBoxList: [], showTip: Tip.NODATA },
    //   });
    // }
  };

  useEffect(() => {
    getList();

  }, []);
  useEffect(() => {
    window.addEventListener('scroll', handleScroll );

    return () => window.removeEventListener('scroll', handleScroll);
  }, [data.searchBoxList]);
  const handleScroll = async () => {
    if (data.totalPage === data.page) {
      return;
    }
    const scrollTop = document.body.scrollTop || document.documentElement.scrollTop;

    if (window.innerHeight + scrollTop + 100 < document.documentElement.offsetHeight) return;
    dispatch({
      type: SHOW_TIP,
      data: {
        showTip: Tip.LOADING
      }
    });
    const res = await getSearchResultApi({ tags: data.twoTab[data.activeTwoTab].type, [data.oneTab[data.activeOneTab].value]: data.oneTab[data.activeOneTab].type, page: data.page + 1 });

    console.log(res, '3333');
    if (res?.item?.length > 0) {
      dispatch({
        type: CHANGE_LIST,
        data: { searchBoxList: [...data.searchBoxList.concat(res.item)], showTip: Tip.SUCCESS, page: data.page + 1 }
      });
    } else {
      dispatch({
        type: SHOW_TIP,
        data: { showTip: Tip.NOMORE },
      });
    }

    console.log('Fetch more list items!');
  };

return (
      <div
          onTouchMove={() => {
        const { top, bottom } = playerRef.current?.getBoundingClientRect();

            setNoSlideArea([{ top, end: bottom, type: 'fixed' }]);
            setH5PageTouchArea(top, bottom, window.innerHeight);
      }} ref={playerRef} className={styles.tabBox} >
        <FirstTab />
        <SecondTab />
      </div>
  );
};
const FirstTab = () => {
  const { dispatch, data } = useContext(ListBoxData);
  const changeOneTab = async (index) => {
    try {
      if (!navigator.onLine) {
        throw new Error('');
      }
      const res = await getSearchResultApi({ tags: data.twoTab[data.activeTwoTab].type, [data.oneTab[index].value]: data.oneTab[index].type });

      if (res.item?.length > 0) {
        dispatch({
          type: CHANGE_LIST,
          data: {
            searchBoxList: res.item,
            activeOneTab: index,
            page: 1,
            showTip: Tip.SUCCESS,
            totalPage: res.totalPage
          }
        });
      } else {
        dispatch({
          type: CHANGE_LIST,
          data: { searchBoxList: [], page: 1, activeOneTab: index, showTip: Tip.NODATA, totalPage: 0 },
        });
      }
    } catch (e) {
      dispatch({
        type: CHANGE_LIST,
        data: { searchBoxList: [], page: 1, activeOneTab: index, showTip: Tip.NOINTERNET, totalPage: 0, clickSearch: () => {
            changeOneTab(index);
          } },
      });
    }

  };

return (
      <div className={styles.firstTab} >
        {data.oneTab.map((item, index) => {
return (
              <div onClick={() => { changeOneTab(index); }} className={data.activeOneTab === index ? styles.activeItem : styles.tabItem} key={index}>
                {item.label}
              </div>
          );
        })}
      </div>
  );
};
const SecondTab = () => {
  const { dispatch, data } = useContext(ListBoxData);

  useEffect(() => {
    const dom = document.querySelector(`#secondTab-${data.activeTwoTab}`);

   dom.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'start' });
  }, []);

  const changeTwoTab = async (index) => {
    try {

      if (!navigator.onLine) {
        throw new Error('');
      }
      const res = await getSearchResultApi({ tags: data.twoTab[index].type, [data.oneTab[data.activeOneTab].value]: data.oneTab[data.activeOneTab].type });

      if (res.item?.length > 0) {
        dispatch({
          type: CHANGE_LIST,
          data: {
            searchBoxList: res.item,
            activeTwoTab: index,
            page: 1,
            showTip: Tip.SUCCESS,
            totalPage: res.totalPage
          }
        });
      } else {
        dispatch({
          type: CHANGE_LIST,
          data: { searchBoxList: [], page: 1, activeTwoTab: index, showTip: Tip.NODATA, totalPage: 0 },
        });
      }
    } catch (e) {
      dispatch({
        type: CHANGE_LIST,
        data: { searchBoxList: [], page: 1, activeTwoTab: index, showTip: Tip.NOINTERNET, totalPage: 0, clickSearch: () => {
            changeTwoTab(index);
          } },
      });
    }

  };

return (<div className={styles.firstTab}>
    {data?.twoTab?.length > 0 && data?.twoTab.map((item, index) => {
return (<div id={`secondTab-${index}`} onClick={() => { changeTwoTab(index); }} className={data.activeTwoTab === index ? styles.activeItem : styles.tabItem} key={index}>
        {item.label}
      </div>);
    })}
  </div>);
};

export default tabBox;
